/* eslint-disable react/display-name */
import React, { useState, forwardRef } from 'react';
import { NavLink as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { ListItem, Button, Collapse, colors } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import Icon from '@material-ui/core/Icon';

//import { MdAccessible } from "react-icons/md";
import { IoMdHome } from "react-icons/io";
import { GoHome } from "react-icons/go";
import { GrConfigure } from "react-icons/gr"
import { VscProject } from "react-icons/vsc"
import { FiUsers } from "react-icons/fi"
import { ImUsers } from "react-icons/im"
import { GoGraph } from "react-icons/go";
import { RiFileList3Line } from "react-icons/ri";
import { AiOutlineSetting } from "react-icons/ai";
import { AiOutlineMessage } from "react-icons/ai";
import { AiOutlineMail } from "react-icons/ai";
import { RiRoadMapLine } from "react-icons/ri";
import { FaLaptopHouse } from "react-icons/fa";
import { RiCustomerService2Fill } from "react-icons/ri";
import { BsTools } from "react-icons/bs";
import { ImSortNumbericDesc } from "react-icons/im";
import { RiUserSettingsLine } from "react-icons/ri";
import { BsShieldLock } from "react-icons/bs";
import { FiKey } from "react-icons/fi";
import { TbReportSearch } from "react-icons/tb";
import { HiDocumentReport } from "react-icons/hi";



const CustomRouterLink = forwardRef((props, ref) => (
  <div
    ref={ref}
    style={{ flexGrow: 1 }}
  >
    <RouterLink {...props} />
  </div>
));

const useStyles = makeStyles(theme => ({
  item: {
    display: 'block',
    paddingTop: 0,
    paddingBottom: 0
  },
  itemLeaf: {
    display: 'flex',
    paddingTop: 0,
    paddingBottom: 0
  },
  button: {
    color: colors.blueGrey[800],
    padding: '10px 8px',
    justifyContent: 'flex-start',
    textTransform: 'none',
    letterSpacing: 0,
    width: '100%'
  },
  buttonLeaf: {
    color: colors.blueGrey[800],
    padding: '10px 8px',
    justifyContent: 'flex-start',
    textTransform: 'none',
    letterSpacing: 0,
    width: '100%',
    fontWeight: theme.typography.fontWeightRegular,
    '&.depth-0': {
      fontWeight: theme.typography.fontWeightMedium
    }
  },
  icon: {
    color: theme.palette.icon,
    display: 'flex',
    alignItems: 'center',
    marginRight: theme.spacing(1),
    height: 24,
    width: 24
  },
  expandIcon: {
    marginLeft: 'auto',
    height: 16,
    width: 16
  },
  label: {
    display: 'flex',
    alignItems: 'center',
    marginLeft: 'auto'
  },
  active: {
    color: theme.palette.primary.main,
    fontWeight: theme.typography.fontWeightMedium,
    '& $icon': {
      color: theme.palette.primary.main
    }
  }
}));

const NavigationListItem = props => {


  const {
    title,
    href,
    depth,
    children,
    icon: Ne,
    className,
    open: openProp,
    label: Label,
    ...rest
  } = props;

  const classes = useStyles();
  const [open, setOpen] = useState(openProp);

  const handleToggle = () => {
    setOpen(open => !open);
  };

  let paddingLeft = 8;

  if (depth > 0) {
    paddingLeft = 32 + 8 * depth;
  }

  const style = {
    paddingLeft
  };

  const getIcon = () => {

    switch (props.icon) {

      case 'GoHome':
        return (<GoHome className={classes.icon} />)

      case 'GrConfigure':
        return (<GrConfigure className={classes.icon} />)

      case 'VscProject':
        return (<VscProject className={classes.icon} />)

      case 'ImUsers':
        return (<ImUsers className={classes.icon} />)

      case 'FiUsers':
        return (<FiUsers className={classes.icon} />)

      case 'FiUsers':
        return (<FiUsers className={classes.icon} />)

      case 'GoGraph':
        return (<GoGraph className={classes.icon} />)

      case 'RiFileList3Line':
        return (<RiFileList3Line className={classes.icon} />)

      case 'AiOutlineSetting':
        return (<AiOutlineSetting className={classes.icon} />)

      case 'AiOutlineMessage':
        return (<AiOutlineMessage className={classes.icon} />)

      case 'AiOutlineMail':
        return (<AiOutlineMail className={classes.icon} />)

      case 'RiRoadMapLine':
        return (<RiRoadMapLine className={classes.icon} />)

      case 'FaLaptopHouse':
        return (<FaLaptopHouse className={classes.icon} />)

      case 'BsTools':
        return (<BsTools className={classes.icon} />)

      case 'ImSortNumbericDesc':
        return (<ImSortNumbericDesc className={classes.icon} />)

      case 'RiUserSettingsLine':
        return (<RiUserSettingsLine className={classes.icon} />)

      case 'BsShieldLock':
        return (<BsShieldLock className={classes.icon} />)

      case 'RiCustomerService2Fill':
        return (<RiCustomerService2Fill className={classes.icon} />)

      case 'FiKey':
        return (<FiKey className={classes.icon} />)

      case 'TbReportSearch':
        return (<TbReportSearch className={classes.icon} />)

      case 'HiDocumentReport':
        return (<HiDocumentReport className={classes.icon} />)


      default:
        break;
    }
    return (<IoMdHome className={classes.icon} />)
  }

  if (children) {

    return (
      <ListItem
        {...rest}
        className={clsx(classes.item, className)}
        disableGutters>
        <Button
          className={classes.button}
          onClick={handleToggle}
          style={style}>
          {getIcon()}
          {title}
          {open ? (
            <ExpandLessIcon
              className={classes.expandIcon}
              color="inherit"
            />
          ) : (
            <ExpandMoreIcon
              className={classes.expandIcon}
              color="inherit"
            />
          )}
        </Button>
        <Collapse in={open}>{children}</Collapse>
      </ListItem>
    );
  } else {

    return (
      <ListItem
        {...rest}
        className={clsx(classes.itemLeaf, className)}
        disableGutters
      >
        <Button
          activeClassName={classes.active}
          className={clsx(classes.buttonLeaf, `depth-${depth}`)}
          component={CustomRouterLink}
          exact
          style={style}
          to={href}
        >
          {/* {Ne &&  <Icon  className={classes.icon} >{props.icon}</Icon>} */}
          {getIcon()}
          {title}
          {Label && (
            <span className={classes.label}>
              <Label />
            </span>
          )}
        </Button>
      </ListItem>
    );
  }
};



NavigationListItem.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  depth: PropTypes.number.isRequired,
  href: PropTypes.string,
  label: PropTypes.any,
  icon: PropTypes.string,
  open: PropTypes.bool,
  title: PropTypes.string.isRequired
};

NavigationListItem.defaultProps = {
  depth: 0,
  open: false
};

export default NavigationListItem;
