import React, { Fragment, useEffect, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/styles';
import { Drawer, Divider, Paper, Avatar, Typography } from '@material-ui/core';
import { Hidden } from '@material-ui/core';
import { useQuery } from "@apollo/react-hooks"

import useRouter from 'utils/useRouter';
import { Navigation } from 'components';
//  import navigationConfig from './navigationConfig';
import config from '../../../../../src/config'
import jwtDecode from 'jwt-decode';
import { getMenuByRole } from '../../../../api/api'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    overflowY: 'auto'
  },
  content: {
    padding: theme.spacing(2)
  },
  profile: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    minHeight: 'fit-content'
  },
  avatar: {
    width: 60,
    height: 60
  },
  name: {
    marginTop: theme.spacing(1)
  },
  divider: {
    marginTop: theme.spacing(2)
  },
  navigation: {
    marginTop: theme.spacing(2)
  }
}))

const NavBar = props => {

  const user = jwtDecode(localStorage.getItem('token'))
  const { openMobile, onMobileClose, className, ...rest } = props;
  const classes = useStyles();
  const router = useRouter();
  const session = useSelector(state => state.session);
  const [navigationConfig, setNavigationConfig] = useState([]);


  useEffect(() => {

    console.log(user);
    if (openMobile) {
      onMobileClose && onMobileClose();
    }
    getMenuByRole({ role: user.role._id, type: "web" }).then(response => {
      console.log(response.menus);
      setNavigationConfig(response.menus);
    })

  }, []);



  const navbarContent = (
    <div className={classes.content}>
      <div className={classes.profile}>
        <Avatar
          style={{ border: '1px solid #cbcbcb' }}
          alt="Person"
          className={classes.avatar}
          component={RouterLink}
          src={`${config.urlImages}/${user.photo}`}
          to="/profile/1/timeline"
        />
        <Typography
          className={classes.name}
          variant="h4"
        >
          {user.username}
        </Typography>
        <Typography variant="body2">{user.rolName}</Typography>
      </div>
      <Divider className={classes.divider} />
      <nav className={classes.navigation}>
        {
          navigationConfig.map((nc, i) => {
            return (
              <Navigation
                key={i}
                component="div"
                pages={nc.pages}
                title={nc.title}
              />
            );
          })
        }

      </nav>
    </div>
  );

  return (
    <Fragment>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
        >
          <div
            {...rest}
            className={clsx(classes.root, className)}
          >
            {navbarContent}
          </div>
        </Drawer>
      </Hidden>
      <Hidden mdDown>
        <Paper
          {...rest}
          className={clsx(classes.root, className)}
          elevation={1}
          square
        >
          {navbarContent}
        </Paper>
      </Hidden>
    </Fragment>
  );
};

NavBar.propTypes = {
  className: PropTypes.string,
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

export default NavBar;
