import React, { useState, useEffect } from 'react';
import validate from 'validate.js';
import clsx from 'clsx';

import { makeStyles } from '@material-ui/styles';
import { Button, TextField } from '@material-ui/core';
import { useMutation } from "@apollo/react-hooks"


import { withRouter } from 'react-router-dom';

import { onLoginUser } from '../../../../api/api';

const schema = {
  username: {
    presence: { allowEmpty: false, message: 'es requerido' }
  },
  password: {
    presence: { allowEmpty: false, message: 'es requerido' }
  }
};

const useStyles = makeStyles(theme => ({
  root: {},
  error: {
    color: 'red'
  },
  fields: {
    margin: theme.spacing(-1),
    display: 'flex',
    flexWrap: 'wrap',
    '& > *': {
      flexGrow: 1,
      margin: theme.spacing(1)
    }
  },
  submitButton: {
    marginTop: theme.spacing(2),
    width: '100%'
  }
}));

var body = document.body
const LoginForm = props => {



  const classes = useStyles();

  const [error, setError] = React.useState(false)
  const [message, setMessage] = React.useState('')
  const [loading, setLoading] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const [formState, setFormState] = useState({
    isValid: false,
    values: {},
    touched: {},
    errors: {}
  });

  useEffect(() => {
    const errors = validate(formState.values, schema);

    setFormState(formState => ({
      ...formState,
      isValid: errors ? false : true,
      errors: errors || {}
    }));
  }, [formState.values]);

  const handleChange = event => {
    event.persist();

    setFormState(formState => ({
      ...formState,
      values: {
        ...formState.values,
        [event.target.name]:
          event.target.type === 'checkbox'
            ? event.target.checked
            : event.target.value
      },
      touched: {
        ...formState.touched,
        [event.target.name]: true
      }
    }));
  }
  const hasError = field => formState.touched[field] && formState.errors[field] ? true : false;


  const handleSubmit = async event => {
    event.preventDefault();

    let data = {
      username: formState.values.username,
      password: formState.values.password
    }

    onLoginUser(data).then(response => {
      setLoading(false);
      if (response.status === 'success') {
        localStorage.setItem("token", response.token);
        body.classList.add("loginStart")
        setTimeout(function () { reload() }, 1500)

      } else {
        setError(true)
        setMessage(response.message)
      }
    }).catch(e => {
      setLoading(false);
    })

    // authenticatePerson({
    //   variables: { username: formState.values.username, password: formState.values.password}
    // }).then( response => 
    // { 
    //     if( response.data.authenticatePerson ) 
    //     {  
    //       if( response.data.authenticatePerson.status )
    //       {
    //         localStorage.setItem("token", response.data.authenticatePerson.token )

    //         body.classList.add("loginStart")

    //         setTimeout(function(){ reload() }, 1500)
    //       }
    //       else
    //       {
    //         setError( true )
    //         setMessage( response.data.authenticatePerson.message )
    //       }
    //     }
    // })

  }


  const reload = () => {
    props.history.push('/home')
    setTimeout(function () {
      body.classList.add('loginEnd')
      setTimeout(function () {
        body.classList.remove('loginEnd')
        body.classList.remove('loginStart')
        window.location.reload()
      }, 1500)
    }, 1500)

  }

  return (
    <form

      className={clsx(classes.root)}
      onSubmit={handleSubmit}
    >
      <div className={classes.fields}>

        <TextField
          autoComplete="off"
          error={hasError('username')}
          fullWidth
          helperText={hasError('username') ? formState.errors.username[0] : null}
          label="Nombre de usuario"
          name="username"
          onChange={handleChange}
          value={formState.values.username || ''}
          variant="outlined" />

        <TextField
          autoComplete="off"
          error={hasError('password')}
          fullWidth
          helperText={
            hasError('password') ? formState.errors.password[0] : null
          }
          label="Contraseña"
          name="password"
          onChange={handleChange}
          type="password"
          value={formState.values.password || ''}
          variant="outlined" />
      </div>
      {
        error
          ? <p className={classes.error}>{message}</p>
          : ''
      }
      <Button
        className={classes.submitButton}
        color="secondary"
        disabled={!formState.isValid}
        size="large"
        type="submit"
        variant="contained">
        Ingresar
      </Button>
    </form>
  );
};



export default withRouter(LoginForm);
