/* eslint-disable react/no-multi-comp */
/* eslint-disable react/display-name */
import React, { lazy } from 'react';
import { Redirect } from 'react-router-dom';

import AuthLayout from './layouts/Auth';
// import ErrorLayout from './layouts/Error';
import DashboardLayout from './layouts/Dashboard';
// import DashboardAnalyticsView from './views/DashboardAnalytics';
// import DashboardDefaultView from './views/DashboardDefault';
// import OverviewView from './views/Overview';
// import PresentationView from './views/Presentation';
import Login from 'views/Login'
const routes = [
  {
    path: '/',
    exact: true,
    render() {
      return (
        <Login />
      );
    }
    //component: () => <Redirect to="/auth/login" />
  },
  {
    path: '/auth',
    component: AuthLayout,
    routes: [
      {
        path: '/auth/login',
        exact: true,
        component: lazy(() => import('views/Login'))
      },
      {
        component: () => <Redirect to="/errors/error-404" />
      }
    ]
  },
  {
    route: '*',
    component: DashboardLayout,
    routes: [
      {
        path: '/attentions',
        exact: true,
        component: lazy(() => import('views/Attentions/Attentions'))
      },
      {
        path: '/maintenances',
        exact: true,
        component: lazy(() => import('views/Projects/Projects'))
      },
      {
        path: '/maintenances-downloaded',
        exact: true,
        component: lazy(() => import('views/Projects/Projects'))
      },
      {
        path: '/home',
        exact: true,
        component: lazy(() => import('views/Home/Home'))
      },
      {
        path: '/users',
        exact: true,
        component: lazy(() => import('views/Person/Person'))
      },

      {
        path: '/customers',
        exact: true,
        component: lazy(() => import('views/Customer/Customers'))
      },
      {
        path: '/roles',
        exact: true,
        component: lazy(() => import('views/Roles/Roles'))
      },
      {
        path: '/roles/to-assign',
        exact: true,
        component: lazy(() => import('views/Roles/AssignRole'))
      },
      {
        path: '/attention-center',
        exact: true,
        component: lazy(() => import('views/CenterOfAttention/CenterOfAttention'))
      },
      {
        path: '/reports/attentions',
        exact: true,
        component: lazy(() => import('views/Reports/ReportServices'))
      },


    ]
  }
];

export default routes;
