import React, { useState, useEffect } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';
import EditIcon from '@material-ui/icons/Edit';
import LockOpenIcon from '@material-ui/icons/LockOpenOutlined';
import PersonIcon from '@material-ui/icons/PersonOutline';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Label } from 'components';
import Chip from '@material-ui/core/Chip';
import config from '../../config';
import './attention.css';

import {
  Card,
  CardHeader,
  CardContent,
  Button,
  Divider,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Typography,
  Grid,
  TextField,
} from '@material-ui/core';
import { Alert, AlertTitle } from '@material-ui/lab';

import { AiOutlineCheckCircle, AiOutlineCloseCircle } from 'react-icons/ai';
import { confirmRejectAttentionCustomer } from '../../api/api';
import LinearProgress from '@material-ui/core/LinearProgress';
import Swal from 'sweetalert2';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

const AttentionDetail = props => {

  const [attention, setAttention] = useState({});
  const [fromNotification, setFromNotification] = useState(false);
  const [openDescription, setOpenDescription] = useState(false);
  const [openButtonConfirmAccept, setOpenButtonConfirmAccept] = useState(false);
  const [openButtonConfirmReject, setOpenButtonConfirmReject] = useState(false);
  const [description, setDescription] = useState("");
  const [statusAttention, setStatusAttention] = useState("");

  const [sending, setSending] = useState(false);


  useEffect(() => {
    setAttention(props.attention);
    setFromNotification(props.fromNotification);
    console.log(props.attention.attentionItems);
  }, [attention, props.attention]);


  const classes = useStyles()



  const onAccept = () => {
    setOpenDescription(true);
    setOpenButtonConfirmAccept(true);
    setStatusAttention("accept");
  };

  const onReject = () => {
    setOpenDescription(true);
    setOpenButtonConfirmReject(true);
    setStatusAttention("reject");

  };

  const onConfirmAcceptReject = () => {
    setSending(true);
    confirmRejectAttentionCustomer({ id: attention._id, statusSend: statusAttention, description }).then(response => {
      console.log(response)
      setSending(false);

      props.onClose();
      props.onRemoveItem(attention);

      Swal.fire({
        icon: 'success',
        title: `La atencion fue aprobada exitosamente`,
        showConfirmButton: false,
        timer: 1500
      });
      if (statusAttention == "accept") {

      }
    });
  };

  const onChangeDescription = e => {
    setDescription(e.target.value);
  };


  return (
    <Card>
      <CardHeader title="Detalle de la atención" />
      <Divider />
      {
        ["send", "resend"].includes(attention.statusSend) ?
          <fieldset>
            <legend>
              <Typography> Información Adicional</Typography>

            </legend>
            <div style={{ width: "50%" }}>


              <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                  <Table size="small">
                    <TableBody>
                      <TableRow>
                        <TableCell>ADMINISTRACION</TableCell>
                        <TableCell>{attention.administracion}</TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>IMPREVISTOS</TableCell>
                        <TableCell>{attention.imprevistos}</TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>UTILIDAD</TableCell>
                        <TableCell>{attention.utilidad}</TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>IVA SOBRE UTILIDAD</TableCell>
                        <TableCell>{attention.ivaSobreUtilidad}</TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>SUBTOTAL</TableCell>
                        <TableCell>{attention.subTotal}</TableCell>
                      </TableRow>
                      <TableRow>
                        <TableCell>TOTAL</TableCell>
                        <TableCell>{attention.total}</TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </Grid>

                <Grid item xs={12} sm={6}>
                  {sending ? <LinearProgress /> : ""}
                  <Grid container spacing={3}>
                    <Grid item xs={12} sm={12}>
                      {
                        statusAttention == "reject" ?
                          <Alert severity="error">
                            <AlertTitle>Rechazar</AlertTitle>
                            Alerta la atencion <strong>{attention.description}</strong> va ser rechazada
                          </Alert> : ""
                      }
                      {
                        statusAttention == "accept" ?
                          <Alert severity="info">
                            <AlertTitle>Aceptar</AlertTitle>
                            Alerta la atencion <strong>{attention.description}</strong> va ser aceptada
                          </Alert> : ""
                      }
                    </Grid>
                    <Grid item xs={12} sm={6} >
                      <Button variant="contained" color="primary" onClick={onAccept} endIcon={<AiOutlineCheckCircle />}>
                        Aprobar
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={6} >
                      <Button variant="contained" color="success" onClick={onReject} endIcon={<AiOutlineCloseCircle />}>
                        Rechazar
                      </Button>
                    </Grid>
                    <Grid item xs={12} sm={12}>
                      {
                        openDescription ?
                          <TextField
                            fullWidth
                            variant='outlined'
                            label="Observaciones"
                            multiline
                            maxRows={4}
                            value={description}
                            onChange={onChangeDescription}
                          /> : ""
                      }
                    </Grid>

                    <Grid
                      item xs={12} sm={12}
                      direction="row" >
                      {
                        openDescription ?
                          <Button
                            color="success"
                            onClick={onConfirmAcceptReject}
                            style={{ float: "right" }}
                            endIcon={<AiOutlineCheckCircle />}>
                            Confirmar
                          </Button>
                          : ""
                      }
                    </Grid>
                  </Grid>

                </Grid>
              </Grid>

            </div>
          </fieldset>
          : ""
      }
      <CardContent className={classes.content}>
        <Table size="small">
          <TableBody>
            <TableRow>
              <TableCell>DESCRIPCIÓN</TableCell>
              <TableCell>{attention.description}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>TIPO DE ATENCIÓN</TableCell>
              <TableCell></TableCell>
            </TableRow>
            <TableRow selected>
              <TableCell>ESTADO</TableCell>
              <TableCell>
                {
                  attention.status === 'finished' ?
                    <Chip label='Finalizado' variant="outlined" /> :
                    <Chip label='Activo' color="primary" variant="outlined" />
                }
              </TableCell>
            </TableRow>

            <TableRow>
              <TableCell>CLIENTE</TableCell>
              <TableCell>{attention.customer ? attention.customer.name : ''}</TableCell>
            </TableRow>


            <TableRow>
              <TableCell colSpan={2}>IMAGENES ANTES DE MANTENIMIENTO</TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={2}>
                <div className='attention-image-wrapper'>
                  {
                    attention.attentionItems ?
                      attention.attentionItems[0].photos.map((photo, i) => {
                        return (
                          <div key={i} className='attention-image'>
                            <img src={`${config.urlImages}${photo.url}`} />
                          </div>);
                      })
                      : <div></div>
                  }
                </div>
              </TableCell >
            </TableRow>


            <TableRow>
              <TableCell colSpan={2}>IMAGENES DESPUES DE MANTENIMIENTO</TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={2}>
                <div className='attention-image-wrapper'>
                  {
                    attention.attentionItems ?
                      attention.attentionItems[1].photos.map((photo, i) => {
                        return (
                          <div key={i} className='attention-image'>
                            <img src={`${config.urlImages}${photo.url}`} />
                          </div>);
                      })
                      : <div></div>
                  }
                </div>
              </TableCell>
            </TableRow>

            <TableRow>
              <TableCell colSpan={2}>FIRMA Y OBSERVACIONES</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>NOMBRE</TableCell>
              <TableCell>{attention.names}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>CEDULA</TableCell>
              <TableCell>{attention.document}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                <div className='attention-image-wrapper'>
                  <div className='attention-signature'>
                    <img src={`${config.urlImages}${attention.signature ? attention.signature : ''}`} />
                  </div>
                </div>
              </TableCell>
              <TableCell>
                {`${attention.description ? attention.description : ''}`}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table >
      </CardContent >



    </Card >
  )
}

export default AttentionDetail