import React, { useState, useEffect } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles';
import EditIcon from '@material-ui/icons/Edit';
import LockOpenIcon from '@material-ui/icons/LockOpenOutlined';
import PersonIcon from '@material-ui/icons/PersonOutline';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Label } from 'components';
import Chip from '@material-ui/core/Chip';
import config from '../../config';
// import './attention.css';

import {
  Card,
  CardHeader,
  CardContent,
  Button,
  Divider,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Typography,
  Grid,
  TextField,
} from '@material-ui/core';
import { Alert, AlertTitle } from '@material-ui/lab';

import { AiOutlineCheckCircle, AiOutlineCloseCircle } from 'react-icons/ai';
import { requestRejectConfirm } from '../../api/api';
import LinearProgress from '@material-ui/core/LinearProgress';
import Swal from 'sweetalert2';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

const RequestDetail = props => {

  const [request, setRequest] = useState({});
  const [fromNotification, setFromNotification] = useState(false);
  const [openDescription, setOpenDescription] = useState(false);
  const [openButtonConfirmAccept, setOpenButtonConfirmAccept] = useState(false);
  const [openButtonConfirmReject, setOpenButtonConfirmReject] = useState(false);
  const [description, setDescription] = useState("");
  const [statusRequest, setStatusRequest] = useState("");

  const [sending, setSending] = useState(false);


  useEffect(() => {
    setRequest(props.request);
  }, [request, props.request]);


  const classes = useStyles()


  const onAccept = () => {
    setOpenDescription(true);
    setOpenButtonConfirmAccept(true);
    setStatusRequest("accept");
  };

  const onReject = () => {
    setOpenDescription(true);
    setOpenButtonConfirmReject(true);
    setStatusRequest("reject");

  };

  const onConfirmAcceptReject = () => {
    setSending(true);
    requestRejectConfirm({ id: request._id, description, statusRequest }).then(response => {
      console.log(response)
      setSending(false);

      props.onClose();
      props.onRemoveItem(request);

      if (response.status == "success") {
        Swal.fire({
          icon: 'success',
          title: response.message,
          showConfirmButton: false,
          timer: 1500
        });
      } else {
        Swal.fire({
          icon: 'error',
          title: response.message,
          showConfirmButton: false,
          timer: 1500
        });
      }
    });
  };

  const onChangeDescription = e => {
    setDescription(e.target.value);
  };
  const getStatus = request => {
    switch (request.status) {
      case "created":
        return <Chip label='Creado por el cliente' color="primary" variant="outlined" />
      case "reject":
        return <Chip label='Rechazdo' color="primary" variant="outlined" />

      default:
        break;
    }
  }


  return (
    <Card>
      <CardHeader title="Detalle de la atención" />
      <Divider />

      <CardContent className={classes.content}>
        <Table size="small">
          <TableBody>
            <TableRow>
              <TableCell style={{ width: 200 }}>DESCRIPCIÓN</TableCell>
              <TableCell>{request.description}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>TIPO DE SOLICITUD</TableCell>
              <TableCell>{request.request_type ? request.request_type.type : ""}</TableCell>
            </TableRow>
            {request.centerOfAttention ? <TableRow>
              <TableCell>CENTRO DE ATENCIÓN</TableCell>
              <TableCell>{request.centerOfAttention.title}</TableCell>
            </TableRow> : ""
            }
            <TableRow selected>
              <TableCell>ESTADO</TableCell>
              <TableCell>
                {
                  getStatus(request)
                }
              </TableCell>
            </TableRow>

            <TableRow>
              <TableCell>CREADOR</TableCell>
              <TableCell>{request.user ? request.user.name : ''}</TableCell>
            </TableRow>

            <TableRow>
              <TableCell>
                <Button variant="contained" color="primary" onClick={onAccept} endIcon={<AiOutlineCheckCircle />}>
                  Aprobar
                </Button>
              </TableCell>

              <TableCell>
                <Button variant="contained" color="success" onClick={onReject} endIcon={<AiOutlineCloseCircle />}>
                  Rechazar
                </Button>
              </TableCell>
            </TableRow>
            {
              statusRequest ?
                <TableRow>
                  <TableCell colSpan={2}>
                    {
                      statusRequest == "reject" ?
                        <Alert severity="error">
                          <AlertTitle>Rechazar</AlertTitle>
                          Alerta la solicitud <strong>{request.description}</strong> va ser rechazada
                        </Alert> : ""
                    }
                    {
                      statusRequest == "accept" ?
                        <Alert severity="info">
                          <AlertTitle>Aceptar</AlertTitle>
                          Alerta la solicitud <strong>{request.description}</strong> va ser aceptada
                        </Alert> : ""
                    }
                  </TableCell>
                </TableRow>
                : ""
            }
            {
              openDescription ?
                <TableRow>
                  <TableCell colSpan={2}>

                    <TextField
                      fullWidth
                      variant='outlined'
                      label="Observaciones"
                      multiline
                      maxRows={4}
                      value={description}
                      onChange={onChangeDescription}
                    />
                  </TableCell>
                </TableRow>
                : ""
            }
            {
              sending ?
                <TableRow>
                  <TableCell colSpan={2}>
                    <LinearProgress />
                  </TableCell>
                </TableRow>
                : ""
            }
            {
              openDescription ?
                <TableRow>
                  <TableCell colSpan={2}>

                    <Button
                      disabled={sending}
                      variant="contained"
                      color="primary"
                      onClick={onConfirmAcceptReject}
                      style={{ float: "right" }}
                      endIcon={<AiOutlineCheckCircle />}>
                      Confirmar
                    </Button>

                  </TableCell>
                </TableRow>
                : ""
            }
          </TableBody>
        </Table >
      </CardContent >



    </Card >
  )
}

export default RequestDetail